module.exports = "import * as React from 'react';\r\nimport { Tooltip, Button } from '../../../../components/';\r\n\r\nexport default function () {\r\n  return (\r\n    <div style={{padding: '30px 100px'}}>\r\n      <div style={{display: 'flex', justifyContent: 'center'}}>\r\n        <Tooltip title=\"\" placement=\"topLeft\"><Button>topLeft</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"top\"><Button>top</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"topRight\"><Button>topRight</Button></Tooltip>\r\n      </div>\r\n      <div style={{display: 'flex', justifyContent: 'space-between'}}>\r\n        <Tooltip title=\"\" placement=\"leftTop\"><Button>leftTop</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"rightTop\"><Button>rightTop</Button></Tooltip>\r\n      </div>\r\n      <div style={{display: 'flex', justifyContent: 'space-between'}}>\r\n        <Tooltip title=\"\" placement=\"left\"><Button>left</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"right\"><Button>right</Button></Tooltip>\r\n      </div>\r\n      <div style={{display: 'flex', justifyContent: 'space-between'}}>\r\n        <Tooltip title=\"\" placement=\"leftBottom\"><Button>leftBottom</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"rightBottom\"><Button>rightBottom</Button></Tooltip>\r\n      </div>\r\n      <div style={{display: 'flex', justifyContent: 'center'}}>\r\n        <Tooltip title=\"\" placement=\"bottomLeft\"><Button>bottomLeft</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"bottom\"><Button>bottom</Button></Tooltip>\r\n        <Tooltip title=\"\" placement=\"bottomRight\"><Button>bottomRight</Button></Tooltip>\r\n      </div>\r\n    </div>\r\n  )\r\n}\r\n"