module.exports = "import * as React from 'react';\nimport { Ripple, Icon, Button } from '../../../../components/';\n\nexport default function () {\n  return (\n    <div>\n      <Ripple color=\"red\" opacity={0.6}><Button type=\"primary\"><Icon type=\"play\"/></Button></Ripple>\n      <Ripple color=\"blue\"><Button type=\"primary\"><Icon type=\"pause\"/></Button></Ripple>\n      <Ripple color=\"yellow\"><Button type=\"primary\"><Icon type=\"skip-backward\"/></Button></Ripple>\n      <Ripple color=\"green\"><Button type=\"primary\"><Icon type=\"skip-forward\"/></Button></Ripple>\n    </div>\n  )\n}\n"