import * as React from 'react';
import { Button } from '../../../../components/';

export default function () {
  return (
    <div>
      <div>
        <Button type="primary" icon="play"></Button>
        <Button type="primary" icon="pause"></Button>
        <Button type="primary" icon="skip-backward"></Button>
        <Button type="primary" icon="skip-forward" tail></Button>
      </div>
      <div style={{marginTop: 20}}>
        <Button type="primary" shape="circle" icon="arrow-up-a"/>
        <Button shape="circle" icon="arrow-up-a"/>
        <Button type="dashed" shape="circle" icon="arrow-up-a"/>
        <Button type="danger" shape="circle" icon="arrow-up-a"/>
      </div>
    </div>
  )
}