import {Component, ReactElement} from 'react';
import {unmountComponentAtNode, render} from 'react-dom';

export interface IProps {
  // tslint:disable
  children: ReactElement<any>;
  boxClass?: string;
  callBack?: () => void; //  - render
  mount?: boolean; //  true
}

export interface IStatus {

}

// HOC
export class RenderInRootDom extends Component<IProps, IStatus> {
  box: HTMLElement;
  renderFlag: boolean = false; // 
  static defaultProps = {
    mount: true,
  }

  renderChild = () => {
    const {boxClass} = this.props;
    this.box = document.createElement("div");
    if (boxClass) {
      this.box.setAttribute('class', boxClass);
    }
    document.body.appendChild(this.box);
    this._renderLayer();
  }

  // children
  componentDidMount() {
    const {mount} = this.props;
    if (mount) {
      this.renderFlag = true;
      this.renderChild();
    }
  }

  // 
  componentDidUpdate() {
    const {mount} = this.props;
    if (mount && !this.renderFlag) {
      this.renderFlag = true;
      this.renderChild();
    } else {
      this._renderLayer();
    }
  }

  // 
  componentWillUnmount() {
    if (this.renderFlag) {
      unmountComponentAtNode(this.box);
      document.body.removeChild(this.box);
    }
  }

  // 
  _renderLayer() {
    if (this.renderFlag) {
      render(this.props.children, this.box, this.props.callBack);
    }
  }

  render() {
    return null;
  }
}
